<template>
	<div id="comment-list">
		<public-header title="我的评价"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group">
					<div class="tab-item"  :class="{active: filter.items.comment_type == 'all'}" @click="onToggleStatus('all')">全部</div>
					<div class="tab-item" :class="{active: filter.items.comment_type == 'good'}" @click="onToggleStatus('good')">满意</div>
					<div class="tab-item" :class="{active: filter.items.comment_type == 'bad'}" @click="onToggleStatus('bad')">不满意</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="comment-list"
				>
					<div class="comment-tips">
						<div class="tips flex">
							<div class="font-15 padding-10-lr font-bold">总评分</div>
							<van-rate v-model="totalComment" :size='18' />
							<div class="font-15 c-gray padding-10-l">{{totalComment}}分</div>
						</div>
					</div>
					<div class="comment-item van-hairline--bottom" v-for="(item, index) in records.data" :key="item.id">
						<div class="flex-lr">
							<div class="flex">
								<div class="c-gray">顾客评价：</div>
								<van-rate v-model="item.delivery_service" :size='18' />
							</div>
							<div class="font-12 c-gray">{{item.addtime_cn}}</div>
						</div>
						<div class="padding-15-t" v-if="item.note">
							{{item.note}}
						</div>
						<div class="padding-10-t flex" v-if="item.deliveryer_tag.length > 0">
							<div class="comment-itag" v-for="(tag, index) in item.deliveryer_tag" :key="tag.id">{{tag}}</div>
						</div>
					</div>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有评价!</p>
				</div>
			</van-pull-refresh>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				totalComment: 4,
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				filter: {
					items: {
						comment_type: 'all'
					}
				},
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad(force){
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'delivery/comment/list',
					success: function(res){
						that.totalComment = res.totalComment;
					}
				})
			},
			onToggleStatus(value) {
				if(this.filter.items.comment_type == value) {
					return;
				}
				this.filter.items.comment_type = value;
			},
			onPullDownRefresh() {
				this.onLoad(true);
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#comment-list .comment-tips{
		padding: 10px 15px;
		background-color: #f1f1f1;
	}
	#comment-list .comment-tips .tips{
		border-left: 2px solid #0EC3B3;
		color: #0EC3B3;
		font-size: 16px;
	}
	#comment-list .comment-item{
		padding: 15px;
		background-color: #fff;
	}
	#comment-list .comment-item .comment-itag{
		font-size: 12px;
		color: #0EC3B3;
		border: 1px solid #0EC3B3;
		border-radius: 3px;
		padding: 2px 5px;
		margin-right: 10px;
	}
</style>


